import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
// import { Link } from "react-router-dom";
import moment from "moment";
import NumberFormat from "../../../App/NumberFormat";

import notification from "../../../../components/notification";
import {
  Empty,
  Collapse,
  Select,
  DatePicker,
  Row,
  Col,
  Divider,
  Input
} from "antd";

// import Popconfirms from "../../../../components/feedback/popconfirm";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/report/uncompletePurchaseOrderReport/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, {
  Box,
  customCollapseStyle,
  TransNo,
  StatusBadge,
  StatusTag
} from "../../../App/app.style";
import PrintWrapper from "../../../App/PrintWrapper";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import ExcelWrapper from "../../../App/ExcelWrapper";
import PDFWrapper from "../../../App/PDFWrapper";
import getLanguage from "../../../App/MyLanguange";
import { getFileName } from "../../../../helpers/helper";
import { siteConfig } from "../../../../settings";

const Panel = Collapse.Panel;
const Option = Select.Option;
const Search = Input.Search;
const { RangePicker } = DatePicker;

const redirectTo = "/dashboard/report-menus";
// const redirectPO = "/dashboard/purchases/purchase-orders";
// const redirectPT = "/dashboard/purchases/purchase-returns";

class UncompletePurchaseOrderReport extends Component {
  state = {
    filter: {
      id_supplier: null,
      id_location: null,
      transaction_status: null,
      id_currency: null,
      start_date: moment()
        .startOf("month")
        .format("YYYY-MM-DD HH:mm:ss"),
      end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
      sort_by: "transaction_date",
      sort_type: "asc"
      // pagination: null
    },
    search: null
  };

  // Get All data diawal
  componentDidMount() {
    const { getUncompletePurchaseOrderReports } = this.props;
    getUncompletePurchaseOrderReports();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.uncompletePurchaseOrderReport.error) {
      notification("error", this.props.uncompletePurchaseOrderReport.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.uncompletePurchaseOrderReport.success) {
      notification("success", this.props.uncompletePurchaseOrderReport.success);
      this.props.clearSuccess();
    }
  }

  // Handle search
  handleSearch = value => {
    const { getUncompletePurchaseOrderReports } = this.props;
    let filter = this.filterValue();

    getUncompletePurchaseOrderReports(1, value, filter, this.state.filter);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getUncompletePurchaseOrderReports } = this.props;
    this.setState({
      filter: {
        id_supplier: null,
        id_location: null,
        transaction_status: null,
        id_currency: null,
        start_date: moment()
          .startOf("month")
          .format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        sort_by: "transaction_date",
        sort_type: "asc"
        // pagination: null
      },
      search: null
    });
    getUncompletePurchaseOrderReports();
  };

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    } else if (name === "id_location") {
      this.setState({
        filter: {
          ...this.state.filter,
          id_location: value,
          id_supplier: null
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  filterValue = () => {
    const { filter } = this.state;
    let string = "";

    if (filter.transaction_status) {
      string += "transaction_status=" + filter.transaction_status;
    }
    if (filter.id_location) {
      string
        ? (string += "&id_location=" + filter.id_location)
        : (string += "id_location=" + filter.id_location);
    }
    if (filter.id_currency) {
      string
        ? (string += "&id_currency=" + filter.id_currency)
        : (string += "id_currency=" + filter.id_currency);
    }
    if (filter.start_date && filter.end_date) {
      string
        ? (string +=
          "&start_date=" + filter.start_date + "&end_date=" + filter.end_date)
        : (string +=
          "start_date=" + filter.start_date + "&end_date=" + filter.end_date);
    }
    if (filter.id_supplier) {
      string
        ? (string += "&id_supplier=" + filter.id_supplier)
        : (string += "id_supplier=" + filter.id_supplier);
    }
    if (filter.pagination) {
      string
        ? (string += "&pagination=" + filter.pagination)
        : (string += "pagination=" + filter.pagination);
    }

    string
      ? (string +=
        "&sort_by=" + filter.sort_by + "&sort_type=" + filter.sort_type)
      : (string +=
        "sort_by=" + filter.sort_by + "&sort_type=" + filter.sort_type);

    return string;
  };

  // Handle filter
  handleFilter = () => {
    const { getUncompletePurchaseOrderReports } = this.props;
    //const { current_page } = this.props.uncompletePurchaseOrderReport;
    let filter = this.filterValue();

    this.setState({ search: null });
    getUncompletePurchaseOrderReports(1, null, filter, this.state.filter);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getUncompletePurchaseOrderReports } = this.props;
    this.setState({
      filter: {
        id_supplier: null,
        id_location: null,
        transaction_status: null,
        id_currency: null,
        start_date: moment()
          .startOf("month")
          .format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        sort_by: "transaction_date",
        sort_type: "asc"
        // pagination: null
      }
    });
    getUncompletePurchaseOrderReports();
  };

  // // Handle pagination
  // handlePageChange = page => {
  //   const { getUncompletePurchaseOrderReports } = this.props;
  //   const { filter } = this.props.uncompletePurchaseOrderReport;
  //   getUncompletePurchaseOrderReports(page, null, filter);
  // };

  render() {
    // console.log(this.props);

    // Data setup
    const {
      uncomplete_purchase_order_reports,
      locations,
      suppliers,
      currencies,
      filter,
      filter_select
    } = this.props.uncompletePurchaseOrderReport;
    //console.log(this.props.uncompletePurchaseOrderReport.base_currency); //console.log(this.state);

    // Definisi kolom2 serta field2 API yang dipakai tabel
    const columns = [
      {
        title: getLanguage({ id: "no." }),
        dataIndex: "key",
        rowKey: "key",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 7,
        render: (text, row) => {
          if (row.key === "Total") {
            return {
              children: <strong style={{ marginLeft: "50%" }}>{text}</strong>,
              props: {
                colSpan: 7
              }
            };
          }

          return text;
        },
        width: "4%"
      },
      {
        title: getLanguage({ id: "purchaseOrders.purchaseOrderNo" }),
        dataIndex: "purchase_order_no",
        rowKey: "purchase_order_no",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 0,
        render: (text, row) => {
          if (row.key === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          let className;
          switch (row.transaction_status) {
            case 1:
              className = "ordered";
              break;
            case 3:
              className = "closed";
              break;
            default:
              break;
          }

          return (
            <div style={{ display: "flex", height: "20px" }}>
              <StatusBadge className={className} />
              <TransNo>{text}</TransNo>
            </div>
          );
          // return <StatusTag className={className}>{display}</StatusTag>;
        },
        width: "14%"
      },
      {
        title: getLanguage({ id: "sidebar.supplier" }),
        dataIndex: "supplier_name",
        rowKey: "supplier_name",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 0,
        render: (text, row) => {
          if (row.key === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return text;
        }
      },
      {
        title: getLanguage({ id: "transactionDate" }),
        dataIndex: "transaction_date",
        rowKey: "transaction_date",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 0,
        date: true,
        render: (text, row) => {
          if (row.key === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>;
        },
        width: "14%"
      },
      {
        title: getLanguage({ id: "dueDate" }),
        dataIndex: "due_date",
        rowKey: "due_date",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 0,
        date: true,
        render: (text, row) => {
          if (row.key === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>;
        }
      },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "transaction_status_string",
        rowKey: "transaction_status_string",
        align: "center",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 0,
        render: (text, row) => {
          if (row.key === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          let className;
          // let display;
          switch (row.transaction_status) {
            case 1:
              className = "ordered";
              break;
            case 3:
              className = "closed";
              break;
            default:
              break;
          }

          return <StatusTag className={className}>{text}</StatusTag>;
        },
        width: "9%"
      },
      {
        title: getLanguage({ id: "payment" }),
        dataIndex: "payment_category_code",
        rowKey: "payment_category_code",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 0,
        render: (text, row) => {
          if (row.key === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return text;
        }
      },
      {
        title: getLanguage({ id: "totalQty" }),
        dataIndex: "total_qty",
        rowKey: "total_qty",
        align: "right",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 1,
        parse: true,
        render: (text, row) => {
          if (row.key === "Total") {
            return (
              <strong>
                <NumberFormat value={parseFloat(text)} />
              </strong>
            );
          }

          return <NumberFormat value={parseFloat(text)} />;
        }
      },
      {
        title: `${getLanguage({ id: "total" })} (IDR)`,
        dataIndex: "total_amount",
        rowKey: "total_amount",
        align: "right",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 1,
        parse: true,
        render: (text, row) => {
          if (row.key === "Total") {
            return (
              <strong>
                <NumberFormat value={parseFloat(text)} />
              </strong>
            );
          }

          return <NumberFormat value={parseFloat(text) * parseFloat(row.currency_rate)} />;
        }
      }
    ];

    const dataSource = [];

    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    let total_qty = 0;
    let total_amount = 0;
    uncomplete_purchase_order_reports.map((data, key) => {
      const totalAmount =
        parseFloat(data.total_amount) * parseFloat(data.currency_rate);
      total_qty += parseFloat(data.total_qty);
      total_amount += totalAmount;

      let string;
      switch (data.transaction_status) {
        case 1:
          string = getLanguage({ id: "ordered" });
          break;
        case 3:
          string = getLanguage({ id: "closed" });
          break;
        default:
          break;
      }

      const modifier = {
        key: key + 1,
        transaction_status_string: string
      };

      dataSource.push({
        ...data,
        ...modifier
      });
    });

    dataSource.push({
      key: "Total",
      total_qty: total_qty,
      total_amount: total_amount
    });

    const {
      loading,
      loadingForeign
    } = this.props.uncompletePurchaseOrderReport;
    const fileName = getLanguage({ id: "uncompletePurchaseOrderReports.pageHeader" })

    //console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={
              <IntlMessages id="uncompletePurchaseOrderReports.pageHeader" />
            }
            marginBottom={true}
          >
            <PrintWrapper
              componentRef={ref => (this.componentRef = ref)}
              title={
                <IntlMessages id="uncompletePurchaseOrderReports.pageHeader" />
              }
              columns={columns}
              data={dataSource}
              filter={filter_select}
              locations={locations}
            />

            <ReactToPrint
              trigger={() => <ButtonPrint loading={loading} />}
              content={() => this.componentRef}
              onBeforePrint={() => (document.title = getFileName(fileName, null, false))}
              onAfterPrint={() => document.title = siteConfig.documentTitle}
              pageStyle={
                "@page { size: landscape; }" +
                "body {" +
                  "-webkit-print-color-adjust: exact;" +
                "} "
              }
            />

            <PDFWrapper
              columns={columns}
              data={dataSource}
              fileName={fileName}
              filter={filter_select}
              landscape={true}
              loading={loading}
              locations={locations}
              scale={0.5}
              title="uncompletePurchaseOrderReports.pageHeader"
            />

            <ExcelWrapper
              title="uncompletePurchaseOrderReports.pageHeader"
              fileName={fileName}
              loading={loading}
              columns={columns}
              data={dataSource}
              filter={filter_select}
              locations={locations}
            />

            <Button
              style={{ marginLeft: "5px" }}
              onClick={() => this.props.history.replace(redirectTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <div className="isoInvoiceTableBtn">
            <Search
              placeholder={`${getLanguage({ id: "purchaseOrders.purchaseOrderNo" })}, ${getLanguage({ id: "sidebar.supplier" })}, ${getLanguage({ id: "sidebar.location" })}, ${getLanguage({ id: "createdBy" })}`}
              onSearch={this.handleSearch}
              style={{ marginRight: "1%" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
            <div
              style={{
                width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                type="primary"
                onClick={this.handleSearch.bind(this, this.state.search)}
              >
                <IntlMessages id="button.searchFilter" />
              </Button>
            </div>

            <div
              style={{
                width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleResetSearch}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>
          </div>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle} defaultActiveKey={["1"]}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="locations.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_location"
                      )}
                      value={
                        this.state.filter.id_location
                          ? this.state.filter.id_location
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={<IntlMessages id="select.location" />}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                    //showArrow={false}
                    >
                      {locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {`${data.location_code} (${data.location_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>

                  <Col span={12}>
                    <label>
                      <IntlMessages id="suppliers.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_supplier"
                      )}
                      value={
                        this.state.filter.id_supplier
                          ? this.state.filter.id_supplier
                          : []
                      }
                      style={{ width: "100%" }}
                      placeholder={
                        this.state.filter.id_location ? (
                          <IntlMessages id="select.supplier" />
                        ) : (
                            <IntlMessages id="noLocation" />
                          )
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                      notFoundContent={
                        this.state.filter.id_location ? (
                          <Empty description={<IntlMessages id="noData" />} />
                        ) : (
                            <Empty
                              description={<IntlMessages id="noLocation" />}
                            />
                          )
                      }
                    >
                      {this.state.filter.id_location
                        ? suppliers.map((data, key) =>
                          data.id_location ===
                            this.state.filter.id_location ? (
                              <Option key={key} value={data.id_supplier}>
                                {`${data.supplier_code} (${data.supplier_name})`}
                              </Option>
                            ) : null
                        )
                        : null}
                    </Select>
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="currencies.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_currency"
                      )}
                      value={
                        this.state.filter.id_currency
                          ? this.state.filter.id_currency
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={<IntlMessages id="currencies.pageHeader" />}
                      optionFilterProp="children"
                      filterOption={true}
                      // loading={this.props.location.loadingFilterSites}
                      allowClear={true}
                    //showArrow={false}
                    >
                      {currencies.map((data, key) => (
                        <Option key={key} value={data.id_currency}>
                          {`${data.currency_code} (${data.currency_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>

                  <Col span={12}>
                    <label>
                      <IntlMessages id="salesOrders.filter.date" />
                    </label>
                    <br />
                    <RangePicker
                      format="DD MMMM YYYY HH:mm:ss"
                      showTime
                      allowClear={false}
                      style={{ width: "100%" }}
                      value={
                        this.state.filter.start_date &&
                          this.state.filter.end_date
                          ? [
                            moment(this.state.filter.start_date),
                            moment(this.state.filter.end_date)
                          ]
                          : []
                      }
                      onChange={this.handleFilterChange.bind(this, "date")}
                    />
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "24px" }}
                >
                  <Col span={12}>
                    <Row justify="start" gutter={1} type="flex">
                      <Col span={12}>
                        <label>
                          <IntlMessages id="stockValuationReports.sortBy" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "sort_by"
                          )}
                          value={
                            this.state.filter.sort_by
                              ? this.state.filter.sort_by
                              : "transaction_date"
                          }
                          style={{ width: "95%" }}
                          // placeholder={
                          //   <IntlMessages id="stockValuationReports.sortBy.select" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                        //allowClear={true}
                        //showArrow={false}
                        >
                          <Option value="transaction_date">
                            <IntlMessages id="uncompletePurchaseOrderReports.filter.date" />
                          </Option>
                          <Option value="purchase_order_no">
                            <IntlMessages id="purchaseOrders.purchaseOrderNo" />
                          </Option>
                          <Option value="location_name">
                            <IntlMessages id="locationName" />
                          </Option>
                          <Option value="supplier_name">
                            <IntlMessages id="supplierName" />
                          </Option>
                          <Option value="total_qty">
                            <IntlMessages id="totalQuantity" />
                          </Option>
                          <Option value="total_amount">
                            <IntlMessages id="total" />
                          </Option>
                        </Select>
                      </Col>
                      <Col span={12}>
                        <label>
                          <IntlMessages id="sortType" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "sort_type"
                          )}
                          value={
                            this.state.filter.sort_type
                              ? this.state.filter.sort_type
                              : "asc"
                          }
                          style={{ width: "90%" }}
                          // placeholder={
                          //   <IntlMessages id="stockValuationReports.sortBy.select" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                        //allowClear={true}
                        //showArrow={false}
                        >
                          <Option key={1} value="asc">
                            <IntlMessages id="ascending" />
                          </Option>
                          <Option key={2} value="desc">
                            <IntlMessages id="descending" />
                          </Option>
                        </Select>
                      </Col>
                    </Row>
                  </Col>
                  <Col span={12}>
                    <Row justify="start" gutter={1} type="flex">
                      <Col span={8}>
                        <label>Status</label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "transaction_status"
                          )}
                          value={
                            this.state.filter.transaction_status
                              ? this.state.filter.transaction_status
                              : undefined
                          }
                          style={{ width: "95%" }}
                          placeholder="Status"
                          optionFilterProp="children"
                          filterOption={true}
                          allowClear={true}
                        //showArrow={false}
                        >
                          <Option value={1}>{getLanguage({ id: "ordered" })}</Option>
                          <Option value={3}>{getLanguage({ id: "closed" })}</Option>
                        </Select>
                      </Col>

                      <Col
                        span={8}
                        style={{
                          marginTop: "auto"
                        }}
                      >
                        <Button
                          size="small"
                          type="primary"
                          style={{ marginRight: "10px" }}
                          onClick={this.handleFilter}
                        >
                          <IntlMessages id="filter.only" />
                        </Button>
                        <Button onClick={this.handleResetFilter} size="small">
                          <IntlMessages id="filter.reset" />
                        </Button>
                      </Col>
                    </Row>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {!filter ? (
              <Empty description={<IntlMessages id="clicksearchFilter" />} />
            ) : uncomplete_purchase_order_reports.length === 0 &&
              !this.props.uncompletePurchaseOrderReport.loading ? (
                  <Empty
                    description={
                      <IntlMessages id="uncompletePurchaseOrderReports.table.noData" />
                    }
                  />
                ) : (
                  <div className="isoInvoiceTable">
                    <Divider orientation="left" style={{ fontSize: 14 }}>
                      <IntlMessages id="details" />
                    </Divider>
                    <Scrollbars style={{ width: "100%" }}>
                      <TableWrapper
                        dataSource={dataSource}
                        columns={columns}
                        loading={loading}
                        className="invoiceListTable"
                        bordered={true}
                        // Set pagination, jika ada page_size mengunakan pagination API
                        // jika tidak ada page_size menggunakan pagination bawaan
                        pagination={false}
                      />
                    </Scrollbars>
                  </div>
                )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    uncompletePurchaseOrderReport: state.uncompletePurchaseOrderReport,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UncompletePurchaseOrderReport);
